<script>
import API from '@/api/'
import { EventBus } from '@/event-bus.js';

export default {
  components: {
    VAnexoDialog: () => import('@/components/AnexoDialog.vue'),
  },

  props: {
    movimentacao: {
      type: Object,
      default: null,
    },
    admin: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },

  data () {
    return {
      loadingDownload: null,
      operacaoMetada: {
        'Dep처sito': { icon: 'file_download', color: 'green', char: '+' },
        'Retirada': { icon: 'file_upload', color: 'red', char: '-' },
      },
      statusColor: {
        'Em progresso': 'blue',
        'Recusado': 'red',
        'Aceito': 'green',
      },
      loadingProcessar: {
        'Recusar': false,
        'Aceitar': false,
      },
    }
  },

  computed: {
    numeroConta () {
      return this.movimentacao.conta[0].numero
    }
  },

  methods: {
    async processar (operacao) {
      if (confirm('Voc챗 deseja prosseguir com a opera챌찾o?')) {
        try {
          this.loadingProcessar[operacao] = true
          const movimentacao = await API.Conta.processarMovimentacao(this.movimentacao._id, operacao)
          // EventBus.$emit('atualizar-movimentacao', movimentacao);
          EventBus.$emit('atualizar-movimentacao', { key: 'status', value: movimentacao });
        } catch (err) {
          let errorMsg = err
          if (err.response.data.message) {
            errorMsg = err.response.data.message
          }
          window.getApp.$emit("APP_GENERIC_WARNING", errorMsg)
        } finally {
          this.loadingProcessar[operacao] = false
        }
      }
    },
  },
}
</script>
